import React, { Component, useState } from 'react';
import Head from 'next/head';
import {connect} from 'react-redux';
import { Modal, ModalHeader, ModalContent, ModalBody, ModalFooter, Button } from 'reactstrap';
import {toggleModal, hideModal, showModal} from '../redux/actions/modalActions';

import Header from '../components/Header';
import Footer from '../components/Footer';
import Player from '../components/Player';

class Layout extends Component {
	static getInitialProps({store}) {}

    constructor(props) {
        super(props);
	}
	
	render() {
		const { children, title } = this.props;

		return (
			<div className="App">
				<Head>
					<meta charSet="utf-8" />
					<meta name="viewport" content="initial-scale=1.0, width=device-width" />

					<title>{title}</title>

					<link rel="shortcut icon" href="favicon.ico" type="image/x-icon" />
				</Head>

				<Header />

				<main class="mt-5 container">
					{/* {children} */}
					{this.props.modal ? 'Mostrando' : 'Escondendo'}
					<br />
					<button onClick={this.props.showModal}>Show</button>
					<button onClick={this.props.hideModal}>Hide</button>
				</main>

				<Player />
				
				<Footer />


				<Modal isOpen={this.props.modal} toggle={this.props.toggleModal}>
					<M>
					Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
					</M>
					<ModalFooter>
					<Button color="primary" onClick={this.props.toggleModal}>Do Something</Button>{' '}
					<Button color="secondary" onClick={this.props.toggleModal}>Cancel</Button>
					</ModalFooter>
				</Modal>

				<Modal isOpen={false} toggle={this.props.toggleModal}>
					<div className="modal-dialog modal-lg modal-dialog-centered" role="document">
						<div className="modal-content">
						<div className="row no-gutters">
							<div className="col-md-6">
							<div className="bg-login">
								<h3>Um lugar divertido com gente incrível.</h3>
							</div>
							</div>
							<div className="col-md-6">
							<div className="toggle-login">
								<div className="login">
								<div className="modal-header">
									<h3 className="modal-title" id="loginModalCenterTitle">Entrar</h3>
									<button type="button" className="close" data-dismiss="modal" aria-label="Close">
									<i className="fas fa-times" />
									</button>
								</div>
								<div className="modal-body">
									<form name="loginform" id="loginform" action="https://marcocuel.com/p/fansite/wp-login.php" method="post">
									<p className="login-username">
										<label htmlFor="user_login">Nome de usuário ou endereço de e-mail</label>
										<input type="text" name="log" id="user_login" className="input" defaultValue size={20} />
									</p>
									<p className="login-password">
										<label htmlFor="user_pass">Senha</label>
										<input type="password" name="pwd" id="user_pass" className="input" defaultValue size={20} />
									</p>
									<p className="login-remember"><label><input name="rememberme" type="checkbox" id="rememberme" defaultValue="forever" /> Lembrar-me</label></p>
									<p className="login-submit">
										<input type="submit" name="wp-submit" id="wp-submit" className="button button-primary" defaultValue="Acessar" />
										<input type="hidden" name="redirect_to" defaultValue="https://marcocuel.com/p/fansite/entrar/" />
									</p>
									</form>
									<hr className="ou my-4" />
									<div className="text-center">
									<a href="https://marcocuel.com/p/fansite/registro" className="btn btn-lg btn-success show-register">Criar nova conta</a>
									</div>
								</div>
								</div>
								<div className="register" style={{display: 'none'}}>
								<div className="modal-header">
									<h3 className="modal-title" id="loginModalCenterTitle">Criar conta</h3>
									<button type="button" className="close" data-dismiss="modal" aria-label="Close">
									<i className="fas fa-times" />
									</button>
								</div>
								<div className="modal-body">
									<form action="https://marcocuel.com/p/fansite/wp-login.php?action=register" method="post">
									<div className="form-group">
										<label>Usuário</label>
										<input type="text" name="user_login" id="user_login" className="form-control alt" />
									</div>
									<div className="form-group">
										<label>E-mail</label>
										<input type="text" name="user_email" id="user_email" className="form-control alt" />
									</div>
									<p className="text-muted">Uma confirmação de registro será enviada para você por e-mail.</p>
									<input className="btn btn-lg btn-block btn-success" type="submit" defaultValue="Cadastre-se" id="register" />
									<div className="text-center text-muted mt-4">
										Já tem uma conta? <a href="https://marcocuel.com/p/fansite/entrar" className="text-link text-primary show-login">Entre agora</a>
									</div>
									</form>
								</div>
								</div>
							</div>
							</div>
						</div>
						</div>
					</div>
				</Modal>

			</div>
		);
	}
}

const mapStateToProps = state => ({
    modal: state.modal.value
});

const mapDispatchToProps = {
    showModal: showModal,
    hideModal: hideModal,
    toggleModal: toggleModal,
};

export default connect(mapStateToProps, mapDispatchToProps)(Layout);